<template>
  <div class="read">
    <el-backtop></el-backtop>
    <div class="read-head" v-show="isHead">
      <van-nav-bar>
        <template #left>
          <div class="right-icon">
            <img
              src="../assets/images/left-white.png"
              alt
              class="auto-img"
              @click="goback"
            />
          </div>
        </template>
        <template #title>
          <div class="classify-title">{{ bookName }}</div>
        </template>
      </van-nav-bar>
    </div>
    <div>
      <img
        :src="item.img"
        alt=""
        class="auto-img"
        @click="showPopup"
        v-for="(item, index) in imgList"
        :key="index"
      />
    </div>
    <div class="read-foot clearfix" v-show="isHead">
      <div class="read fl" @click="lastChapter">
        <div></div>
      </div>
      <div class="dir fl">
        <div class="dir-img" @click="openDir">
          <img src="../assets/images/dir.png" alt="" class="auto-img" />
        </div>
        <div class="num">
          {{ Number(this.index) + 1 }}/{{ this.dirList.length }}
        </div>
      </div>
      <div class="bookrack fr" @click="nextChapter">
        <div></div>
      </div>
    </div>
    <van-popup
      v-model="show"
      position="left"
      :style="{ width: '70%', height: '100%' }"
    >
      <div
        class="count-list"
        v-for="(item, i) in dirList"
        :key="i"
        ref="fontColor"
        id="fontColor"
        @click="selectList(i)"
        :class="{ active: i === index }"
      >
        {{ item.num }}
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/read.less";
export default {
  data() {
    return {
      // 
      isHead: false,
      // 
      show: false,
      // 
      bookName: "",
      // 
      dirList: "",
      // url
      urlbook: "",
      // 
      imgList: "",
      // 
      index: 0,
      //
      tag: "",
      //
      sigh: "",
      //
      find: "",
      // 
      title:"",
      //url
      mhlb:"",
      //
      logo:"",
      //
      nolook:"",
    };
  },

  created() {
    //
    this.getData();
    
  },

  methods: {
    // 
    getData() {
      
      // 
      document.documentElement.scrollTop = 0;
      // 
      this.urlbook = this.$route.query.urlbook;
      this.bookName = this.$route.query.bookName;
      // 
      this.index = this.$route.query.index;
      
      this.mhlb = this.$route.query.mhlb;
      this.title = this.$route.query.title;
      //
      this.logo = this.$route.query.logo;
      this.find = this.$route.query.find;
      this.tag = this.$route.query.tag;
      if (this.bookName) {
        
        let Bookrack = JSON.parse(localStorage.getItem("Bookrack"));
        Bookrack.forEach((v) => {
          if (v.data.name == this.bookName) {
            this.dirList = v.list;
          }
        });
        //
        this.axios({
          mrthods: "GET",
          url: "http://47.114.147.221",
          params: {
            mhurl2: this.dirList[this.index].url,
          },
        })
          .then((res) => {
            
            if (res.data.code == 1) {
              this.$toast("");
               this.nolook="No";
              this.goback();
              return;
            }
            this.imgList = res.data.list;
            
          })
          .catch(() => {});
      } else {
        //
        this.axios({
          mrthods: "GET",
          url: "http://47.114.147.221",
          params: {
            mhurl1: this.urlbook,
          },
        })
          .then((res) => {
            if (!res.data.data.cover) {
              return;
            }
            let urlbookArr = this.urlbook.split("/");
            // 
            if (urlbookArr[0] == "mh123" && urlbookArr[1] == "comic") {
              this.dirList = res.data.list.reverse();
            }
            this.bookName=res.data.data.name;
            this.dirList = res.data.list;
            //  
            this.axios({
              mrthods: "GET",
              url: "http://47.114.147.221",
              params: {
                mhurl2: this.dirList[this.index].url,
              },
            })
              .then((res) => {
                if (res.data.code == 1) {
                  this.$toast("");
                  this.nolook="No";
                  this.goback();
                  return;
                }
                this.imgList = res.data.list;
                
              })
              .catch(() => {});
          })
          .catch(() => {});
      }

      // 
      this.tag = this.$route.query.tag;
      //
      this.sigh = this.$route.query.sigh;
      

    },

    // 
    selectList(index) {
      this.index = index;
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      //
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhurl2: this.dirList[this.index].url,
        },
      })
        .then((res) => {
          
          if (res.data.code == 1) {
            this.$toast("");
             this.nolook="No"
            this.goback();
            return;
          }
          this.$toast.clear();
          this.imgList = res.data.list;
          
        })
        .catch(() => {});
    },

    // 
    showPopup() {
      this.isHead = !this.isHead;
    },
    // 
    goback() {
      // 
      if (this.sigh) {
        this.$router.push({ name: "Bookrack" });
        // 
        let progress = JSON.parse(localStorage.getItem("Progress"));
        //
        if (!progress) {
          let pro = [];
          pro.push({ bookName: this.bookName, index: this.index });
          localStorage.setItem("Progress", JSON.stringify(pro));
          return;
        }
        //
        let progressIndex = progress.findIndex((v) => {
          return v.bookName == this.bookName;
        });
        if (progressIndex != -1) {
          progress[progressIndex].index = this.index;
          localStorage.setItem("Progress", JSON.stringify(progress));
          return;
        }
        progress.unshift({ bookName: this.bookName, index: this.index });
        localStorage.setItem("Progress", JSON.stringify(progress));
        return;
      }
      //
      this.$router.push({
        name: "Detail",
        query: {
          bookurl: this.urlbook,
          tag: this.tag,
          logo:this.logo,
          title:this.title,
          mhlb:this.mhlb,
          nolook:this.nolook,
          find:this.find
        },
      });
    },
    // 
    openDir() {
      this.show = true;
      //
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhurl2: this.dirList[this.index].url,
        },
      })
        .then((res) => {
          this.imgList = res.data.list;
        })
        .catch(() => {});
    },
    //
    lastChapter() {
      if (Number(this.index) <= 0) {
        this.$toast("");
        return;
      }
      this.index--;
      //
      document.documentElement.scrollTop = 0;
      //
      this.selectList(this.index);
    },
    // 
    nextChapter() {
      if (Number(this.index) + 1 >= this.dirList.length) {
        this.$toast("");
        return;
      }
      this.index++;
      // 
      document.documentElement.scrollTop = 0;
      //
      this.selectList(this.index);
    },
  },
};
</script>

<style lang="less" scoped>
</style>